.video-embed {
    position: relative;
    &-media-container {
        margin-bottom: 1em;
        .card & {
            @extend %bleeding-container-top;
        }
    }
    &-iframe-container {
        @extend %iframe-embed-container;
        > * {
            @extend %responsive-media;
        }
    }
    &-details {
        @extend %layout-stacked;
        &[data-state="visible"] {
            opacity: 1;
            z-index: 1;
            &[data-animation="video-play"] {
                animation: growBand 1s forwards ease-in;
            }
            &[data-animation="video-close"] {
                animation: shrinkBand 1s forwards ease-in;
            }
        }
        &[data-state="hidden"] {
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            opacity: 0;
            width: 100%;
            &[data-animation="video-play"] {
                animation: opacity .5s .2s forwards ease-in;
            }
            &[data-animation="video-close"] {
                animation: opacity .5s forwards ease-in;
            }
        }
    }
    &-title {
        @extend %default-level3;
    }
    &-summary-link {
        @extend %default-link;
    }
    &-summary {
        @extend %default-paragraph;
    }
}

@keyframes growBand {
    0% {
        opacity: 1;
        z-index: 1;
        min-height: 0;
    }
    50% {
        opacity: 0;
        z-index: 1;
        min-height: 550px;
    }
    100% {
        opacity: 0;
        z-index: -1;
        min-height: 550px;
    }
}
@keyframes shrinkBand {
    0% {
        opacity: 1;
        z-index: 1;
        max-height: 550px;
    }
    50% {
        opacity: 0;
        z-index: -1;
        max-height: 550px;
    }
    100% {
        opacity: 0;
        z-index: -1;
        max-height: 50px;
    }
}
@keyframes opacity {
    50% {
        opacity: 0;
        z-index: -1;
    }
    100% {
        opacity: 1;
        z-index: 1;
    }
}
