@import "default";
@import "vendor/magnific-popup";

.main-wrap {
    background: url("/img/background/bkgd-stars-2000x1150.jpg") center center;
    background-color: color("black");
    background-attachment: fixed;
    background-size: cover;
}

[data-band-background="pink-trees"] {
    background: url("/img/background/bkgd-branches-2000x1288.jpg") top right;
    background-color: color("light-pink");
    background-attachment: fixed;
    background-size: cover;
}
[data-band-background="dark-purple"] {
    background-color: color("dark-purple");
}
.image-embed {
    &-img {
        .popup-link:hover & {
            cursor: zoom-in;
        }
    }
}
.card {
    &[data-background="blue"] {
        background: color("dark-blue");
    }
    &[data-background="opaque"] {
        background: color("black", .4);
    }
}
.card-header {
    [data-background="blue"] &-headline {
        color: color("white");
    }
    [data-background="white"] &-headline {
        color: color("dark-blue");
    }
}
.content {
    b {
        font-size: 1.2em;
        font-weight: 400;
    }
    .extra {
        font-size: 1.4em;
        font-weight: 900;
        line-height: 1;
    }
}
