@import "themes";
%default-paragraph {
    @extend %standard-text-themes;
    font-family: $base-font-family;
    display: block;
    font-size: 1em;
    font-weight: normal;
    line-height: 1.44;
    margin: 0 0 1em;
    &:last-child {
        margin-bottom: 0;
    }
}
%default-pre {
    @extend %standard-text-themes;
    font-family: 'PT Mono', monospace;
    display: block;
    font-size: 1em;
    font-weight: normal;
    line-height: 1.44;
    margin: 0 0 1em;
    &:last-child {
        margin-bottom: 0;
    }
}
%default-link {
    @extend %standard-link-themes;
    font-family: $base-font-family;
    text-decoration: none;
    font-weight: 400;
    &[name]:not([href]) {
        color: inherit;
    }
    &:hover {
        text-decoration: underline;
    }
    &:visited {
        text-decoration: line-through;
    }
}
%icon-link {
    @extend %standard-text-themes;
    text-decoration: none;
    font-size: 22px;
    font-weight: 800;
    &[name]:not([href]) {
        color: inherit;
    }
    &:visited {
        text-decoration: line-through;
    }
}
%default-ul {
    margin: 0 0 1.5em;
    &:last-child {
        margin-bottom: 0;
    }
}
%default-li {
    @extend %standard-text-themes;
    font-family: $base-font-family;
    font-size: 1em;
    margin: 0;
    line-height: 1.44;
}
%default-ol {
    margin: 0 0 1.5em;
    &:last-child {
        margin-bottom: 0;
    }
}
%default-dl {
    line-height: 1.2;
    margin-bottom: 1.5em;
    &:last-child {
        margin-bottom: 0;
    }
}
%default-dt {
    @extend %standard-headline-themes;
    font-family: $base-font-family;
    font-weight: 600;
    margin-top: 0;
    font-size: 1em;
}
%default-dd {
    @extend %standard-text-themes;
    font-family: $base-font-family;
    margin: 0 0 1em;
    font-size: 1em;
    margin-left: 0;
}
%default-th {
    @extend %standard-headline-themes;
    font-family: $base-font-family;
    font-size: 1em;
    padding: 10px 20px 10px 0;
    border-collapse: collapse;
    border: 0;
}
%default-td {
    @extend %standard-text-themes;
    font-family: $base-font-family;
    font-size: 1em;
    padding: 10px 20px 10px 0;
    border-collapse: collapse;
    border: 0;
    border-bottom: 1px solid;
}
%cta-special-hover {
    position: relative;
    display: inline-block;
    outline: none;
    vertical-align: bottom;
    padding: 0 0.25em;
    color: color("red");
    text-decoration: none;
    &::before,
    &::after {
        position: absolute;
        top: 0;
        font-weight: 100;
        font-size: 150%;
        line-height: 1;
        opacity: 0;
        transition: opacity 0.2s, transform 0.2s;
        backface-visibility: hidden;
        font-smoothing: antialiased;
        vertical-align: bottom;
    }
    &::before {
        content: '[';
        left: -0.5rem;
        transform: translateX(-100%);
        @include breakpoint($screen-sm-min) {
            left: -0.1em;
        }
    }
    &::after {
        content: ']';
        right: -0.5rem;
        transform: translateX(100%);
        @include breakpoint($screen-sm-min) {
            right: -0.1em;
        }
    }
    &:hover,
    &:focus {
        border-bottom: 0px;
        &::before,
        &::after {
            opacity: 1;
            transform: translateX(0);
        }
    }
}
%cta-secondary-link {
    @extend %standard-link-themes;
    font-family: $base-font-family;
    font-weight: 800;
    text-decoration: none;
    cursor: pointer;
    padding-right: 15px;
    &:hover {
        text-decoration: none;
    }
    &:after {
        @include arrow($type: right, $width: 7px, $height: 6px, $pseudo: true) {
            top: 40%;
        }
        margin-left: .5em;
        line-height: 100%;
        vertical-align: middle;
    }
}
%cta-tertiary-link {
    @extend %standard-headline-themes;
    font-family: $base-font-family;
    font-weight: 800;
    text-decoration: none;
    cursor: pointer;
    padding-right: 15px;
    &:hover {
        text-decoration: none;
    }
    &:after {
        @include arrow($type: right, $width: 7px, $height: 6px, $pseudo: true) {
            top: 40%;
        }
        margin-left: .5em;
        line-height: 100%;
        vertical-align: middle;
    }
}
%cta-button-link {
    @include make-button( $background: color("red"), $text: color("white"));
}
%cta-ghost-link {
    @include make-button( $background: transparent, $text: color("white"), $border: 1px );
}
%cta-button-disabled {
    @include make-button(
		$background: color("light-gray"),
		$text: color("dark-gray")
	);
}
%cta-video-play {
    font-family: $base-font-family;
    font-size: 1em;
    text-transform: uppercase;
    font-weight: 400;
    text-align: center;
    text-decoration: none;
    color: color("black");
    &:after {
        display: inline-block;
        position: relative;
        content: "\25b6";
        padding: 8px 6px 8px 12px;
        font-size: 25px;
        line-height: 30px;
        border: 2px solid color("black");
        width: 50px;
        height: 50px;
        border-radius: 50%;
        vertical-align: middle;
        margin-left: 15px;
		[data-theme~="dark"] & {
			border-color: color("white");
		}
    }
    [data-theme~="dark"] & {
        color: color("white");
    }
    &:hover {
        opacity: 50%;
        cursor: pointer;
    }
}
%quote-default {
    @extend %standard-text-themes;
    font-family: 'Inconsolata', $base-font-family;
    margin-top: 0;
    line-height: 1.2;
    font-style: italic;
    font-size: 1.1em;
    @include breakpoint($screen-sm-min) {
        font-size: 1.8em;
    }
}
%quote-footer {
    @extend %standard-text-themes;
    font-family: $base-font-family;
    font-size: pem(14);
    line-height: 1.142;
    margin: 0;
    text-transform: uppercase;
}
