@import "themes";

%section-title {
    @extend %emphasis-themes;
    font-family: $base-font-family;
    font-size: 22px;
    margin-bottom: 10px;
    text-transform: uppercase;
    font-weight: 800;
    margin-top: 0;
    line-height: 1.4;
    [data-text-align="right"] & {
        text-align: right;
    }
    [data-text-align="left--right"] & {
        text-align: left;
        @include breakpoint($screen-sm-min) {
            text-align: right;
        }
    }
    [data-text-align="sm-md--center"] & {
        text-align: center;
        @include breakpoint($screen-md-min) {
            text-align: left;
        }
    }
}
%section-headline {
    @extend %standard-headline-themes;
    font-family: $base-font-family;
    font-size: 32px;
    margin-bottom: 10px;
    font-weight: 800;
    margin-top: 0;
    line-height: 1.1;
    @include breakpoint( $screen-md-min) {
        font-size: 39px;
    }
    @include breakpoint( $screen-lg-min) {
        font-size: 42px;
    }
    a {
        text-decoration: none;
        color: inherit;
        &:hover {
            color: color("dark-blue");
        }
    }
    [data-text-align="right"] & {
        text-align: right;
    }
    [data-text-align="left--right"] & {
        text-align: left;
        @include breakpoint($screen-sm-min) {
            text-align: right;
        }
    }
    [data-text-align="sm-md--center"] & {
        text-align: center;
        @include breakpoint($screen-md-min) {
            text-align: left;
        }
    }
}
%section-summary {
    @extend %standard-text-themes;
    font-family: $base-font-family;
    font-size: 1.2em;
    line-height: 1.38;
    margin-top: 0;
    a {
        color: color("light-gray");
        &:hover {
            cursor: pointer;
			color: color("blue");
        }
		&:visited {
			text-decoration: line-through;
		}
    }
    [data-text-align="right"] & {
        text-align: right;
    }
    [data-text-align="left--right"] & {
        text-align: left;
        @include breakpoint($screen-sm-min) {
            text-align: right;
        }
    }
    [data-text-align="sm-md--center"] & {
        text-align: center;
        @include breakpoint($screen-md-min) {
            text-align: left;
        }
    }
	.extra {
		font-size: 1.4em;
	}
}
