.page-wrapper {
    background-color: color("light-teal");
}
.menu {
    &-wrap {
        position: fixed;
        font-weight: 700;
        width: 100%;
        transition: transform .5s;
        transform: translateX(-300px);
        &[data-menu-state="open"] {
            transform: translateX(0px);
            z-index: 99;
        }
    }
    &-top {
        line-height: 58px;
        .profile {
            display: inline-flex;
            align-items: center;
        }
    }
    &-side,
    &-icon-list {
        width: 300px;
    }
    &-side-link {
        display: block;
        padding: 1.2em;
        border-bottom: 1px solid color("white", .1);
        &:first-child {
            border-top: 1px solid color("white", .1);
        }
    }
    &-side-link,
    &-icon-item {
        @extend %section-title;
        @extend %standard-headline-themes;
        text-decoration: none;
        cursor: pointer;
        &:hover {
            color: color("dark-blue");
            transition: color .3s;
        }
    }
    &-icon {
        &-list {
            @include flex(
                $align: both
            );
            margin: $box-padding/2 0;
        }
        &-item {
            margin: 0 1em 0 0;
            padding: 0.5em;
            > * {
                font-size: 2em;
            }
        }
    }
    &-button {
        position: fixed;
        z-index: 1000;
        margin: .5em;
        padding: 0;
        width: 2.5em;
        height: 2.25em;
        border: none;
        text-indent: 2.5em;
        font-size: 1.5em;
        color: transparent;
        background: transparent;
        transition: transform .3s;
        &::before {
            content: '';
            position: absolute;
            top: 0.5em;
            right: 0.5em;
            bottom: 0.5em;
            left: 0.5em;
            background: linear-gradient(color("white") 20%, transparent 20%, transparent 40%, color("white") 40%, color("white") 60%, transparent 60%, transparent 80%, color("white") 80%);
        }
        &:hover {
            opacity: 0.6;
            cursor: pointer;
        }
        &[data-state="open"] {
            transform: translate3d(300px, 0, 0);
        }
    }
}
