%standard-headline-themes {
    &,
    [data-theme="white"] & {
        color: color("black");
    }
    [data-theme~="dark"] & {
        color: color("white");
    }
}
%standard-text-themes {
    &,
    [data-theme="white"] & {
        color: color("dark-gray");
    }
    [data-theme~="dark"] & {
        color: color("white");
    }
}
%standard-link-themes {
    &,
    [data-theme="white"] & {
        color: color("dark-blue");
        &:hover {
            color: darken(color("dark-blue"), 10%);
        }
    }
    [data-theme~="dark"] & {
        color: color("light-blue");
    }
}
%emphasis-themes {
    &,
    [data-theme="white"] & {
        color: color("blue");
    }
    [data-theme~="dark"] & {
        color: color("white");
    }
}
%component-background-themes {
    &,
    [data-theme="white"] & {
        background-color: color("white");
    }
    &[data-theme="dark"] {
        background-color: color("black");
    }
    &[data-theme="blue"] {
        background-color: color("dark-blue");
    }
    &[data-theme="gray"] {
        background: color("light-gray");
    }
}
%band-background-themes {
    &,
    [data-band-background="white"] & {
        background-color: color("white");
    }
    &[data-band-background="transparent"] {
        background-color: transparent;
    }
    &[data-band-background="light-blue"] {
        background-color: color("blue");
    }
    &[data-band-background="blue"] {
        background-color: color("dark-blue");
    }
    &[data-band-background="dark"] {
        background-color: color("black");
    }
    &[data-band-background="opaque"] {
        background: color("black", .5);
    }
    &[data-band-background="gray"] {
        background: color("light-gray");
    }
    &[data-band-background="pink"] {
        background: color("pink");
    }
}
