.cta {
    position: relative;
    &[data-state="hidden"] {
        display: none;
    }
	&[data-align="center-center"] {
	    @include flex(
			$align: center,
			$justify: center
		);
	}
    &-link {
		&:hover {
			cursor: pointer;
		}
        &[data-hover="special"] {
            @extend %cta-special-hover;
        }
        &[data-cta-type="button"] {
            @extend %cta-button-link;
        }
        &[data-cta-type='video-play'] {
			@extend %cta-video-play;
            margin-top: 0;
        }
        &[data-cta-type="secondary"] {
            @extend %cta-secondary-link;
            &:hover {
                opacity: .9;
            }
        }
        &[data-cta-type="tertiary"] {
            @extend %cta-tertiary-link;
            &:hover {
                opacity: .9;
            }
        }
        &[data-cta-type="disabled"] {
            @extend %cta-button-disabled;
        }
        &[data-cta-type="ghost"] {
            @extend %cta-ghost-link;
        }
    }
    // content horizontal align values
    &[data-text-align="left"] {
        text-align: left;
    }
    &[data-text-align="right"] {
        text-align: right;
    }
    &[data-text-align="center"] {
        text-align: center;
    }
}
