.quote {
    quotes: "\201C""\201D""\2018""\2019";
    &-container {
        @extend %default-reset; // reset in case blockquote is used
        display: flex;
        flex-flow: row nowrap;
        + .ux-cta {
            font-style: normal;
        }
        &:before {
            @include breakpoint($screen-sm-min) {
                content: open-quote;
                float: left;
                font-size: 5em;
                padding-right: 10px;
                margin-top: -50px;
                margin-bottom: -50px;
                font-size: 7em;
            }
        }
    }
    &-quotation {
        @extend %quote-default;
    }
    &-reference {
        @extend %quote-footer;
    }
    &-attribution {
        font-weight: bold;
        text-align: right;
    }
}
