/*doc
---
title: Colors
category: Global - Colors
---
To use a color use the function of color("variable-name") like:

- `background: color("rich-black");`
- `color: color("white");`
*/

$site-colors: (
    "white":        #fff,
    "cream":        #FCFBE3,
    "light-gray":   #dadada,
    "dark-gray":    #4c4c4c,
    "black":        #252527,
    "rich-black":   #0e0e0e,
    "pure-black":   #000,
    "gold":         #FFDD1B,
    "red":          #d04c3f,
    "dark-red":     #cd1414,
    "dark-purple":  #4c1f36,
	"pink": #f27676,
    "light-pink":   #F2A287,
    "blue":         #0088ce,
    "light-blue":   #08c0fc,
    "dark-blue":    #004153,
    "light-teal":   #6BA6A5,
);
