.image-embed {
    &-container {
        width: 100%;
        [data-align='bleed'] & {
            @extend %bleeding-container;
            background: inherit; // inherit background color of parent;
        }
        // external context - internal context - selector
        [data-align='bleed']:first-child & {
            @extend %bleeding-container-top;
            background: inherit; // inherit background color of parent;
        }
        [data-img-shape="circle"] & {
            position: relative;
            perspective: 1000;
            transform-style: preserve-3d;
            @include breakpoint(max-width $screen-sm-max) {
                max-width: 350px;
                margin: 0 auto;
            }
        }
    }
    // Image Tag
    &-img {
        max-width: 100%;
        // used when not 'full', but larger than container
        display: block;
        height: auto;
        [data-align='full'] &,
        [data-align='bleed'] & {
            @extend %responsive-media;
        }
        [data-align='left'] & {
            margin-right: auto;
        }
        [data-align='right'] & {
            margin-left: auto;
        }
        [data-align='center'] & {
            margin-right: auto;
            margin-left: auto;
        }
        [data-scale='reduced'] & {
            transform: scale(.75);
        }
        // hover transitions
        [data-img-hover='bw-color'] & {
            @include transition(all);
            filter: grayscale(100%);
        }
        [data-img-hover='bw-color']:hover & {
            filter: grayscale(0);
        }
        [data-img-hover='color-bw'] & {
            @include transition(all);
            filter: grayscale(0);
        }
        [data-img-hover='color-bw']:hover & {
            filter: grayscale(100%);
        }
        [data-img-shape='circle'] & {
            border-radius: 50%;
        }
    }
    &-svg {
        width: 100%;
        // used when not 'full', but larger than container
        display: block;
        height: auto;
        [data-align='full'] &,
        [data-align='bleed'] & {
            @extend %responsive-media;
        }
        [data-align='left'] & {
            margin-right: auto;
        }
        [data-align='right'] & {
            margin-left: auto;
        }
        [data-align='center'] & {
            margin-right: auto;
            margin-left: auto;
        }
        [data-scale='reduced'] & {
            transform: scale(.75);
        }
    }
    &-caption {
        @extend %default-paragraph;
    }
}
