.card {
    @extend %component-background-themes;
    @include flex(
        $flow: column wrap
    );
    position: relative;
    max-width: 100%;
    padding: $box-padding;
    text-decoration: none;
    transition: background .3s;
    &[data-justify="center"] {
        justify-content: center;
    }
    &[data-justify="top"] {
        justify-content: flex-start;
    }
    &[data-corners="rounded"] {
        border-radius: 15px;
    }
    &[data-padding="half"] {
        padding: $box-padding/2;
    }
    &[data-align="center"] {
        align-items: center;
    }
    &[data-outline="border"] {
        border: 1px solid color("black");
    }
    &[data-background-hover] {
        cursor: pointer;
    }
    > *,
    &-header > *,
    &-body > *,
    &-footer > * {
        flex: 0 0 auto;
        margin: 0 0 $box-padding/2;
        width: 100%; // explicit width needed for IE10 bug
        &:last-child {
            margin-bottom: 0;
        }
    }
    [data-justify="justify"] > *:nth-last-child( 2 ) {
        flex-grow: 1;
    }
    &-header {
        &-headline {
            @extend %default-level4;
             // content horizontal align values
             [data-text-align="left"] & {
                 text-align: left;
             }
             [data-text-align="right"] & {
                 text-align: right;
             }
             [data-text-align="center"] & {
                 text-align: center;
             }
        }
    }
    &-body {
        [data-justify="justify"] & {
            flex: 1 0 auto;
        }
    }
    &-footer {}
}
